.item {
    width: 280px;
}

.item p {
    margin: 6px 0px;
}

.item-prices {
    display: flex;
    gap: 20px;
}

.item img {
    width: 250px;
}

.item-prices-new {
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}

.item-prices-old {
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover {
    transform: scale(1.05);
    transition: 0.6s;
}

@media(max-width:1280px) {
    .item {
        width: 290px;
        font-size: 14px;
    }

    .item img {
        width: 290px;
    }

    .item-prices-old {
        font-size: 14px;
    }
}

@media(max-width:1024px) {
    .item {
        width: 230px;
        font-size: 13px;
    }

    .item img {
        width: 230px;
    }

    .item-prices-old {
        font-size: 13px;
    }
}


